<!--  -->
<template>
 <div>
   <form class="ffff" @click="action1">
     <input class="aaaa" type="button" :value="nameb" style="color">
   </form>
 </div>
</template>

<script>
export default {
  props: {
    nameb: String,
    color: String
  },
  methods: {
    action1 () {
      alert(this.nameb)
    }
  }
}
</script>
<style>
  .aaaa {
    position: relative;
    width: 200px;
    height: 60px;
    font-size: 30px;
    color: red;
    background: blue;
    border-color: yellow;
    border-width: 8px;
  }
  .inp {
    border-width: 3.0;
  }
</style>



