import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import MainItem from '@/components/MainItem/MainItem'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'MainItem',
      component: MainItem
    },
    {
      path: '/a',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



