<template>
  <div class="ha">
    <table>
      <tr v-for="(value) in source" :key="value.id"><MainListCell :nameb="value" /></tr>
    </table>
    <div>
      <ul>
        <li v-for="value in source" :key="value.id">{{ value }}</li>
      </ul>
    </div>
    <div>
      <ZBCardListCell class="ZBCardListCell" @detail="card_detail" v-for="card in cards" :key="card.title" @click.native="actionaa(card)" :titleName="card.title" :content="card.content" :card_no="card.card_no"></ZBCardListCell>
    </div>
  </div>
</template>

<script>

import MainListCell from '@/components/MainItem/components/MainListCell'
import ZBCardListCell from '@/components/MainItem/components/ZBCardListCell'

export default {
  // 注册引入自定义组件
  components: {
    MainListCell,
    ZBCardListCell
  },
  data () {
    return {
      source: ['hello world', 'pretty boy', 'god is a girl', 'god is a girl'],
      cards: [
        {title: '刘帅', content: '北京中关村银行 I 类户', card_no: '622626888888888180'},
        {title: '李欣', content: '中国人民银行 II 类户', card_no: '622626666666666180'},
        {title: '白羊', content: '中国建设银行 III 类户', card_no: '622626555555555180'},
        {title: '王术全', content: '中国农业银行 I 类户', card_no: '622626555555555180'}]
    }
  },
  methods: {
    actionaa (item) {
      alert(item.title + '+' + item.content + '+' + item.card_no)
    },
    card_detail (a) {
      alert(a)
    }
  }
}
</script>
<style>
  .tr {
    background: yellow;
  }
  ul {
    color: yellow;
  }
  li {
    border-color: red;
    border-width: 2px;
    border-style: solid;
    color: blue;
    text-align: left;
  }
</style>



