<!--  -->
<template>
 <div id="cell">
   <div class="titleaa">
     <span class="ititleName">{{ titleName }}</span>
     <span class="icontent">{{ content }}</span>
     <span class="idetail" @click="detail" @click.stop="actionaa">详情</span>
   </div>
   <div>
      <span class="icard_no">{{ card_no }}</span>
   </div>
 </div>
</template>

<script>
export default {
  props: {
    titleName: String,
    content: String,
    card_no: String
  },
  methods: {
    detail () {
      this.$emit('detail', this.titleName + this.content + this.card_no)
    }
  }
}

</script>

<style>
  #cell {
     border-style: solid;
     border-width: 1.0px;
     border-color: rgb(128, 128, 128);
     border-radius: 4px;
     background: rgb(211, 211, 198);
     position: relative;
     height: 80px;
     left: 20px;
     width: 300px;
  }
  .titleaa {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
  }
  .ititleName {
     color: rgb(212, 44, 44);
     font-size: 16px;
     font: medium;
  }
  .icontent {
     color: rgb(22, 19, 19);
     font-size: 16px;
     font: optional;
  }
  .idetail {
     color: rgb(3, 128, 76);
     font-size: 14px;
     font: optional;
  }
  .icard_no {
    display: flex;
    justify-content: left;
    margin-top: 10px;
    padding-left: 16px;
    font: bolder;
    font-size: 20px;
  }
</style>



